/*-----------Fade in welcome Text------------*/
.welcome {
  -webkit-animation: fadeIn 9s;
          animation: fadeIn 9s;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

@-webkit-keyframes fadeIn {
  0% { opacity: 0; }
  30% { opacity: 0;}
  100% { opacity: 1; }
}

@keyframes fadeIn {
  0% { opacity: 0; }
  30% { opacity: 0;}
  100% { opacity: 1; }
}

@-webkit-keyframes fadefirst {
  0% { opacity: 0; }
  20% { opacity: 0;}
  100% { opacity: 1; }
}

@keyframes fadefirst {
  0% { opacity: 0; }
  20% { opacity: 0;}
  100% { opacity: 1; }
