/*-----------------------------Header Animations-------------------------------*/
.content {
  position: relative;
  font-family: "Lato", sans-serif;
  font-size: 35px;
  line-height: 40px;
  color: black;
  width: 70%;
}

.containers {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: smaller;
  overflow: hidden;
}
.containers p {
  font-family: 'Raleway', sans-serif;
}

.containers:before {
  content: "[";
  right: 20px;
}

.containers:after {
  content: "]";
  left: 20px;
}

.containers:after,
.containers:before {
  position: relative;

  color: var(--lightblue);
  line-height: 40px;

  -webkit-animation-name: opacity;
  -webkit-animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  animation-name: opacity;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

.text {
  display: inline;
}
.closetext {
  display: inline;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.list {
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  list-style: none;
  color: #1da1f2;
  margin: 0px 10px 0px 10px;
  -webkit-animation-name: change;
  -webkit-animation-duration: 12s;
  -webkit-animation-iteration-count: infinite;
  animation-name: change;
  animation-duration: 12s;
  animation-iteration-count: infinite;
}

.item {
  line-height: 40px;
  margin: 0;
}

@-webkit-keyframes opacity {
  0%,
  100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@keyframes opacity {
  0%,
  100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@-webkit-keyframes change {
  0%, 
  15% {
    -webkit-transform: translate3d(0, -37%, 0);
            transform: translate3d(0, -37%, 0);
  }
  30% {
    -webkit-transform: translate3d(0, -12%, 0);
            transform: translate3d(0, -12%, 0);
  }
  45% {
    -webkit-transform: translate3d(0, 12%, 0);
            transform: translate3d(0, 12%, 0);
  }
  60% {
    -webkit-transform: translate3d(0, 36%, 0);
            transform: translate3d(0, 36%, 0);
  }
  75% {
    -webkit-transform: translate3d(0, 12%, 0);
            transform: translate3d(0, 12%, 0);
  }
  90% {
    -webkit-transform: translate3d(0, -13%, 0);
            transform: translate3d(0, -13%, 0);
  }
  100% {
    -webkit-transform: translate3d(0, -37%, 0);
            transform: translate3d(0, -37%, 0);
  }
}

@keyframes change {
  0%, 
  15% {
    -webkit-transform: translate3d(0, -37%, 0);
            transform: translate3d(0, -37%, 0);
  }
  30% {
    -webkit-transform: translate3d(0, -12%, 0);
            transform: translate3d(0, -12%, 0);
  }
  45% {
    -webkit-transform: translate3d(0, 12%, 0);
            transform: translate3d(0, 12%, 0);
  }
  60% {
    -webkit-transform: translate3d(0, 36%, 0);
            transform: translate3d(0, 36%, 0);
  }
  75% {
    -webkit-transform: translate3d(0, 12%, 0);
            transform: translate3d(0, 12%, 0);
  }
  90% {
    -webkit-transform: translate3d(0, -13%, 0);
            transform: translate3d(0, -13%, 0);
  }
  100% {
    -webkit-transform: translate3d(0, -37%, 0);
            transform: translate3d(0, -37%, 0);
  }
}

:root {
  --heavyblue: #2e3047;
  --mediumblue: #3c3f58;
  --lightblue: #707793;
  --mint: #3bba9c;
  --gold: #ffd700;
  --jeanblue: #5D76A9;
  --main: #EAE7DC;
  --grey: #8E8D8A;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  html {
    scroll-behavior: smooth;
  }
  
  body {
    color: white;
    background-color: #3c3f58;
    background-color: var(--mediumblue);
    font-family: "Quicksand", sans-serif;
    height: 100%;
    scroll-behavior: smooth 1s;
  }
  
  main {
    transition: padding-left 0.5s;
    justify-content: center;
    height: 100%;
    align-items: center;
  }
  
  /*------------  Scroll Properties  ------------*/
section {
    height: 100vh;
    scroll-snap-align: center;
    transition: all ease-in-out 1s;
  }
  
  .one {
    background-color: #EAE7DC;
    background-color: var(--main);
  }
  
  .two {
    background-color: white;
  }
  
  .three {
    background-color: #EAE7DC;
    background-color: var(--main);
  }
  

  /*------------  Welcome Section Styling  ------------*/

.welcome {
  display: flex;
  justify-content: center;
  width: 100%;
  position: relative;
  place-items: center;
  margin-top: 100px;
  color: black;
}

  /* For Desktop View */
@media screen and (min-width: 1024px) {
  .root {
    background-color: #63c971;
    color: #fff;
  }
}
 
/* For Tablet View */
@media screen and (min-device-width: 768px)
and (max-device-width: 1024px) {
  .root {
    width: 400px;
    height: 400px;
    background-color: orange;
    color: black;
  }
}
 
/* For Mobile Portrait View */
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .root {
    width: 200px;
    height: 200px;
    background-color: red;
    color: #fff;
  }
}
 
/* For Mobile Landscape View */
@media screen and (max-device-width: 640px)
and (orientation: landscape) {
  .root {
    width: 400px;
    height: 200px;
    background-color: cyan;
    color: black;
  }
}
 
/* For Mobile Phones Portrait or Landscape View */
@media screen and (max-device-width: 640px) {
  .root {
    width: 400px;
    height: 200px;
    background-color: chartreuse;
    color: black;
  }
}
 
/* For iPhone 4 Portrait or Landscape View */
@media screen and (min-device-width: 320px)
and (-webkit-min-device-pixel-ratio: 2) {
  .root {
    width: 400px;
    height: 400px;
    background-color: brown;
    color: black;
  }
}
 
/* For iPhone 5 Portrait or Landscape View */
@media (device-height: 568px) and (device-width: 320px)
and (-webkit-min-device-pixel-ratio: 2) {
  .root {
    width: 400px;
    height: 400px;
    background-color: cornflowerblue;
    color: black;
  }
}
 
/* For iPhone 6 and 6 plus Portrait or Landscape View */
@media (min-device-height: 667px) and (min-device-width: 375px)
and (-webkit-min-device-pixel-ratio: 3) {
  .root {
    width: 400px;
    height: 400px;
    background-color: darkgoldenrod;
    color: black;
  }
}

