.abut {
  display: block;
  width: 70px;
  height: 70px;
  border-radius: 70px;
  line-height: 70px;
  font-weight: bold;
  text-align: center;
  transition: all 0.35s;
  background-color: var(--heavyblue);
  color:#1da1f2;
}

.icon {
  visibility: hidden;
  width: 70px;
  height: 70px;
  border: 3px solid transparent;
  position: absolute;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  right: 0;
  top: 0;
  z-index: -1;
  transition: 2s;

}

.icon svg {
  width: 42px;
  position: absolute;
  top: calc(50% - 21px);
  left: calc(50% - 21px);
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  fill: var(--lightblue);
  transition: 1s;
  
}

.icon svg:hover {
    transition: 1s;
    background-color:#1da1f2;
}

.abut:hover {
  width: 100px;
  border: 3px solid var(--lightblue);
  border-radius: 560px;
  color: var(--lightblue);
  transition: all 1s;
}

.abut:hover + .icon {
  visibility: visible;
  border: 3px solid var(--lightblue);
  right: -60%;
  transition: ease-in .35s;
}

